	module.exports = {"data":{"markdownRemark":{"html":"<blockquote>\n<p>\"« Je désire que vous parliez tous en langues, mais encore plus que vous prophétisiez. Celui qui prophétise est plus grand que celui qui parle en langues, à moins que ce dernier n’interprète, pour que l’Église en reçoive de l’édification. En est-il qui parlent en langue, que deux ou trois au plus parlent, chacun à son tour, et que quelqu'un interprète; s’il n’y a point d’interprète, qu’on se taise dans l’Église, et qu’on parle à soi-même et à Dieu. » <br> <small>- 1 Corinthiens 14:5‭, ‬27-28</small></p>\n</blockquote>\n<p>Paul déclare ici qu'il désirerait certes que tous parlent en langues si une telle chose était possible, mais encore plus que tous prophétisent ! <em>(il parle ici au conditionnel puisque comme nous l'avons vu, Paul enseigne que tous ne reçoivent pas le même don)</em>.</p>\n<p>De manière encore plus intéressante, le chapitre de <strong>1 Corinthiens 12</strong> se termine par le verset suivant :</p>\n<blockquote>\n<p>« Aspirez aux dons les meilleurs. Et je vais encore vous montrer une voie par excellence. » <strong><em>(verset 31)</em></strong>  </p>\n</blockquote>\n<p>Cette voie par excellence est présentée par Paul <strong>au chapitre 13</strong>, comme celle qui surpasse tout, y compris le don des langues et celui de prophétie ! Quelle est donc cette chose encore plus excellente que tous les dons de l'Esprit cités précédemment selon Paul ? <strong>C'est l'amour agape !</strong></p>\n<blockquote>\n<p>« L'amour ne meurt jamais. Les prophéties disparaîtront, les langues cesseront, la connaissance disparaîtra. Maintenant donc ces trois choses demeurent: la foi, l'espérance, l'amour; mais la plus grande de ces choses, c'est l'amour. » <br> <small>- 1 Corinthiens 13:8,13</small></p>\n</blockquote>","frontmatter":{"title":"Quel don Paul considère-t-il préférable au don des langues ?"}}},"pathContext":{"slug":"/quel-don-paul-considere-t-il-preferable-au-don-des-langues/"}}

